const kono_voice = new Audio(kono_voice_url)
const ha_voice = new Audio(ha_voice_url)

export default function generateVoices(agari: Agari): any {
    const voices = new Array<any>()
    agari.Point.MatchYakusForMessage.forEach((yaku: YakuForMessage) => {
        switch (yaku.Name) {
            case "断么九":
                voices.push(tanyao_voice)
                break
            case "立直":
                voices.push(reach_voice)
                break
            case "一発":
                voices.push(ippatsu_voice)
                break
            case "門前自摸":
                voices.push(tsumo_voice)
                break
            case "槍槓":
                voices.push(chankan_voice)
                break
            case "嶺上開花":
                voices.push(rinshan_voice)
                break
            case "海底摸月":
                voices.push(haitei_voice)
                break
            case "河底撈魚":
                voices.push(houtei_voice)
                break
            case "ダブルリーチ":
                voices.push(doublereach_voice)
                break
            case "七対子":
                voices.push(chitoi_voice)
                break
            case "連風東":
                voices.push(dabuton_voice)
                break
            case "連風南":
                voices.push(dabunan_voice)
                break
            case "連風西":
                voices.push(dabusha_voice)
                break
            case "連風北":
                voices.push(dabupe_voice)
                break
            case "三暗刻":
                voices.push(sananko_voice)
                break
            case "三槓子":
                voices.push(sankantsu_voice)
                break
            case "四暗刻単騎":
                voices.push(suankotanki_voice)
                break
            case "純正九蓮宝燈":
                voices.push(junseichuren_voice)
                break
            case "国士無双一三面待ち":
                voices.push(kokushi13_voice)
                break
            case "平和":
                voices.push(pinhu_voice)
                break
            case "白":
                voices.push(haku_voice)
                break
            case "發":
                voices.push(hatsu_voice)
                break
            case "中":
                voices.push(chun_voice)
                break
            case "東":
                voices.push(ton_voice)
                break
            case "南":
                voices.push(nan_voice)
                break
            case "西":
                voices.push(sha_voice)
                break
            case "北":
                voices.push(pe_voice)
                break
            case "対々和":
                voices.push(toitoi_voice)
                break
            case "三色同刻":
                voices.push(sanshokudoukou_voice)
                break
            case "三色同順":
                voices.push(sanshokudoujun_voice)
                break
            case "混老頭":
                voices.push(honroto_voice)
                break
            case "一気通貫":
                voices.push(ittsu_voice)
                break
            case "混全帯么九":
                voices.push(chanta_voice)
                break
            case "小三元":
                voices.push(shousangen_voice)
                break
            case "混一色":
                voices.push(honitsu_voice)
                break
            case "純全帯么九":
                voices.push(junchan_voice)
                break
            case "清一色":
                voices.push(chinitsu_voice)
                break
            case "緑一色":
                voices.push(ryuiso_voice)
                break
            case "大三元":
                voices.push(daisangen_voice)
                break
            case "小四喜":
                voices.push(shosushi_voice)
                break
            case "字一色":
                voices.push(tsuiso_voice)
                break
            case "国士無双":
                voices.push(kokushi_voice)
                break
            case "四暗刻":
                voices.push(suanko_voice)
                break
            case "清老頭":
                voices.push(chinroto_voice)
                break
            case "四槓子":
                voices.push(sukantsu_voice)
                break
            case "大四喜":
                voices.push(daisushi_voice)
                break
            case "九蓮宝燈":
                voices.push(churen_voice)
                break
            case "二盃口":
                voices.push(ryanpeko_voice)
                break
            case "一盃口":
                voices.push(ipeko_voice)
                break
            case "流し満貫":
                voices.push(nagashimangan_voice)
                break
            case "天和":
                voices.push(tenho_voice)
                break
            case "地和":
                voices.push(chiho_voice)
                break
            case "人和":
                voices.push(renho_voice)
                break
            case "九種九牌":
                voices.push(kyushukyuhai_voice)
                break
            case "ドラ":
                voices.push(dora_voice)
                break
            case "裏ドラ":
                voices.push(uradora_voice)
                break
            case "抜きドラ":
                voices.push(nukidora_voice)
                break
        }
    })

    if (agari.Oya) {
        voices.push(oyano_voice)
    } else {
        voices.push(kono_voice)
    }

    if (agari.Point.Han >= 13) {
        voices.push(yakuman_voice)
    } else {
        switch (agari.Point.Hu) {
            case 20:
                voices.push(hu20_voice)
                break
            case 25:
                voices.push(hu25_voice)
                break
            case 30:
                voices.push(hu30_voice)
                break
            case 40:
                voices.push(hu40_voice)
                break
            case 50:
                voices.push(hu50_voice)
                break
            case 60:
                voices.push(hu60_voice)
                break
            case 70:
                voices.push(hu70_voice)
                break
            case 80:
                voices.push(hu80_voice)
                break
            case 90:
                voices.push(hu90_voice)
                break
            case 100:
                voices.push(hu100_voice)
                break
            case 110:
                voices.push(hu110_voice)
                break
        }
        switch (agari.Point.Han) {
            case 1:
                voices.push(han1_voice)
                break
            case 2:
                voices.push(han2_voice)
                break
            case 3:
                voices.push(han3_voice)
                break
            case 4:
                voices.push(han4_voice)
                break
            case 5:
                voices.push(han5_voice)
                break
            case 6:
                voices.push(han6_voice)
                break
            case 7:
                voices.push(han7_voice)
                break
            case 8:
                voices.push(han8_voice)
                break
            case 9:
                voices.push(han9_voice)
                break
            case 10:
                voices.push(han10_voice)
                break
            case 11:
                voices.push(han11_voice)
                break
            case 12:
                voices.push(han12_voice)
                break
        }
    }

    voices.push(ha_voice)

    switch (Math.floor(agari.Point.Point / 10000) % 10) {
        case 1:
            voices.push(n10000_voice)
            break
        case 2:
            voices.push(n20000_voice)
            break
        case 3:
            voices.push(n30000_voice)
            break
        case 4:
            voices.push(n40000_voice)
            break
        case 5:
            voices.push(n50000_voice)
            break
        case 6:
            voices.push(n60000_voice)
            break
        case 7:
            voices.push(n70000_voice)
            break
        case 8:
            voices.push(n80000_voice)
            break
        case 9:
            voices.push(n90000_voice)
            break
    }
    switch (Math.floor(agari.Point.Point / 1000) % 10) {
        case 1:
            voices.push(n1000_voice)
            break
        case 2:
            voices.push(n2000_voice)
            break
        case 3:
            voices.push(n3000_voice)
            break
        case 4:
            voices.push(n4000_voice)
            break
        case 5:
            voices.push(n5000_voice)
            break
        case 6:
            voices.push(n6000_voice)
            break
        case 7:
            voices.push(n7000_voice)
            break
        case 8:
            voices.push(n8000_voice)
            break
        case 9:
            voices.push(n9000_voice)
            break
    }
    switch (Math.floor(agari.Point.Point / 100) % 10) {
        case 1:
            voices.push(n100_voice)
            break
        case 2:
            voices.push(n200_voice)
            break
        case 3:
            voices.push(n300_voice)
            break
        case 4:
            voices.push(n400_voice)
            break
        case 5:
            voices.push(n500_voice)
            break
        case 6:
            voices.push(n600_voice)
            break
        case 7:
            voices.push(n700_voice)
            break
        case 8:
            voices.push(n800_voice)
            break
        case 9:
            voices.push(n900_voice)
            break
    }
    return voices
}
